
.header {
    width: 100%;
    background-color: var(--primary-main-color);
    padding: 12px 0;
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;
}

.iconsMenu {
    display: flex;
    column-gap: 10px;
}

.favouritesLink {
    position: relative;
    width: 34px;
    height: 34px;
}

.iconsMenu svg {
    width: 100%;
    height: 100%;
}

.iconsMenu svg:hover {
    cursor: pointer;
    opacity: .8;
}

.iconBubble {
    background-color: #01A54E;
    border-radius: 50%;
    color: #FFFFFF;
    font-size: 14px;
    width: 22px;
    height: 22px;
    line-height: 1.25;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    position: absolute;
    bottom: 60%;
    left: 70%;
}


.wrapperEntry {
    color: var(--text-main-color);
    text-decoration: none;
    padding: 8px 16px;
}

.wrapperEntry:hover {
    opacity: .8;
}

@media (max-width: 700px) {
    .wrapper {
        flex-direction: column;
        align-items: stretch;
        gap: 20px;
    }
}

